#game-and-title{display:flex;flex-direction:column;min-width:1100px;height:800px;position:relative;z-index:2}#game-title{text-align:center;font-size:24px;color:white;margin-top:20px;margin-bottom:0;font-family:'Fjalla One', sans-serif}#game-and-title-start{width:0px;height:0px;display:none}#main-canvas{box-shadow:0 0 5px 2px;max-width:1100px;max-height:700px;position:absolute;top:60px}#start-menu{width:1100px;height:700px;max-width:1100px;max-height:700px;display:flex;flex-direction:column;justify-content:center;align-items:center;position:absolute;top:60px;background:black}#play-button{border-radius:10px;min-width:200px;min-height:100px;box-shadow:0 0 5px 2px;font-size:20px;text-align:center;line-height:100px;background:gray;font-family:'Fjalla One', sans-serif}#play-button:hover{background:black;color:white;cursor:pointer}#instructions{border-radius:10px;padding:20px;min-width:200px;min-height:250px;background:gray;color:black;text-align:center;display:flex;flex-direction:column;justify-content:space-between;margin-top:50px;font-family:'Fjalla One', sans-serif;z-index:10}#title{text-align:center;font-size:24px;color:white;margin-top:0;margin-bottom:0;font-family:'Fjalla One', sans-serif;z-index:5}#landing-menu-visible{display:absolute;top:375px;z-index:5}img{width:100%;height:100%;position:absolute;object-fit:cover;z-index:1}#pause-button{position:absolute;color:black;font-family:'Fjalla One', sans-serif;top:70px;left:1050px;z-index:3}html{height:100%;width:100%}.main{display:flex;flex-direction:column;width:100%;height:0;justify-content:space-between;align-items:center;align-content:center;transition:height 2s ease-in-out}body{width:100%;height:100%;position:relative;display:flex;justify-content:center;align-items:center}.no-display{display:none}.splash-container{display:flex;justify-content:center;align-items:center;height:100px;width:200px;margin-right:30px;background:none;z-index:8;border-radius:10px;transition:background .5s}.splash-logo{color:white;font-size:30px;font-family:'Fjalla One', sans-serif}#instructions-backdrop{height:0;width:0;position:absolute;top:60px;background:gray;z-index:10}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}
